<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
        @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
        @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">
                    <el-col :span="10">
                        <span class="techo-list__header-col-txt-4">中文姓名：</span>
                        <div class="techo-list__header-col-left-4">
                        <el-input placeholder="请输入中文姓名" :maxlength="10" size="small" class="techo-list__header-select-full"
                            clearable v-model="queryForm.chinessName"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :height="'60%'" :title="title"></techo-details>
        <vAddDialog ref="vAddDialog" :disCodeOption="disCodeOption"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/infact_people_service.js";
import vAddDialog from "./overseas-people-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "境外人员",
      queryForm: {
        districtCode: "",
        chinessName: ""
      },
      header: {
        外文名: "foreignName",
        中文姓名: "chinessName",
        // 外文姓: "foreignSurname",
        性别: "dicMap.sex",
        // 出生日期: "birthName",
        "国籍(地区)": "dicMap.nationality",
        // 宗教信仰: "dicMap.religiousBelief",
        // 证件类型: "dicMap.certificateCode",
        // 证件号码: "certificateNumber",
        // 证件有效期: "certificateValidityTime",
        联系方式: "phone",
        来华目的: "dicMap.comingToChina",
        // 职业类别: "dicMap.occupationType",
        // 职业: "occupationDesc",
        // 服务处所: "servicePlace",
        // 现住地: "livePlace",
        现住地详址: "livePlaceDetail",
        抵达日期: "arriveTime",
        预计离开日期: "leaveTime",
        是否重点关注人员: "dicMap.isFocus"
      },
      data: [],
      recordCount: 0,
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "overseasPeople"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },
  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加境外人员 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
    },
    /** 删除境外人员 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteOverseasPeople(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "境外人员删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "境外人员删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      let data = _.cloneDeep(row);
      if (data.occupationType != null && data.occupationType != "") {
        let occType = [
          data.occupationType.substr(0, 1) + "0000",
          data.occupationType.substr(0, 3) + "00",
          data.occupationType
        ];
        data.occupationType = _.uniq(occType);
      } else {
        data.occupationType = [];
      }
      this.$refs["vAddDialog"].dialogForm = data;
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        districtCode: constants.REGION.CODE,
        chinessName: ""
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageOverseasPeople(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageOverseasPeople(pageInfo, data => {
            this.recordCount = data.totalCount;
            _.map(data.list, item => {
              item.dicMap &&
                (item.dicMap.isFocus = item.isFocus == 1 ? "是" : "否");
            });
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          _.map(data.list, item => {
            item.dicMap &&
              (item.dicMap.isFocus = item.isFocus == 1 ? "是" : "否");
          });
          this.data = data.list;
        }
      });
    }
  }
};
</script>
