<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen :modal="false">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
            <el-row :gutter="15">
              <el-col :span="6">
                <el-form-item label="外文名" prop="foreignName">
                    <el-input v-model="dialogForm.foreignName" :maxlength="10" placeholder="请填写外文名"></el-input>
                </el-form-item>
                <el-form-item label="抵达日期" prop="arriveTime">
                    <el-date-picker v-model="dialogForm.arriveTime" type="date" placeholder="请选择日期"></el-date-picker>
                </el-form-item> 
                <el-form-item label="是否重点关注人员" prop="isFocus" label-width="140px">
                    <el-radio-group v-model="dialogForm.isFocus">
                        <el-radio :label="1">是</el-radio>
                        <el-radio :label="0">否</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="证件类型">
                    <el-select placeholder="请选择证件类型" clearable v-model="dialogForm.certificateCode">
                        <el-option v-for="(item, index) in dic['证件代码']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item> 
              </el-col>
              <el-col :span="6">
                <el-form-item label="性别" prop="sex">
                    <el-select placeholder="请选择性别" clearable v-model="dialogForm.sex">
                        <el-option v-for="(item, index) in dic['性别']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>  
                <el-form-item label="预计离开日期" prop="leaveTime">
                    <el-date-picker v-model="dialogForm.leaveTime" type="date" placeholder="请选择日期"></el-date-picker>
                </el-form-item>
                <el-form-item label="国籍(地区)">
                    <el-select placeholder="请选择国籍(地区)" clearable v-model="dialogForm.nationality">
                        <el-option v-for="(item, index) in dic['国籍']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="证件有效期">
                    <el-date-picker v-model="dialogForm.certificateValidityTime" type="date" placeholder="请选择日期"></el-date-picker>
                </el-form-item> 
              </el-col>
              <el-col :span="6">
                <el-form-item label="联系方式" prop="phone">
                    <el-input v-model="dialogForm.phone" :maxlength="30" placeholder="请填写联系方式"></el-input>
                </el-form-item>
                <el-form-item label="来华目的" prop="comingToChina">
                    <el-select placeholder="请选择来华目的" clearable v-model="dialogForm.comingToChina">
                        <el-option v-for="(item, index) in dic['来华目的']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item> 
                <el-form-item label="宗教信仰">
                    <el-select placeholder="请选择宗教信仰" clearable v-model="dialogForm.religiousBelief">
                        <el-option v-for="(item, index) in dic['宗教信仰']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item> 
                <el-form-item label="证件号码">
                    <el-input v-model="dialogForm.certificateNumber" :maxlength="30" placeholder="请填写证件号码"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="外文姓">
                    <el-input v-model="dialogForm.foreignSurname" :maxlength="10" placeholder="请填写外文姓"></el-input>
                </el-form-item>
                <el-form-item label="中文姓名" prop="chinessName">
                    <el-input v-model="dialogForm.chinessName" :maxlength="10" placeholder="请填写中文名"></el-input>
                </el-form-item>
                <el-form-item label="出生日期">
                    <el-date-picker v-model="dialogForm.birthName" type="date" placeholder="请选择日期"></el-date-picker>
                </el-form-item> 
                <el-form-item label="服务处所">
                    <el-input v-model="dialogForm.servicePlace" :maxlength="25" placeholder="请填写服务处所"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="15">
              <el-col :span="12">
                <el-form-item label="职业类别">
                    <el-cascader placeholder="请选择职业类别" :options="dic['职业类别']" 
                        filterable change-on-select clearable 
                        v-model="dialogForm.occupationType" :props="{value: 'code'}"></el-cascader>
                </el-form-item> 
              </el-col>
              <el-col :span="6">
                <el-form-item label="职业">
                    <el-input v-model="dialogForm.occupationDesc" :maxlength="30" placeholder="请填写职业"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item label="现住地">
                    <el-select placeholder="请选择现住地" clearable disabled v-model="dialogForm.livePlace">
                        <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                        
                    </el-select>
                </el-form-item> 
              </el-col>
            </el-row>
            <el-row :gutter="15">
              <el-col :span="6">
                <el-form-item prop="houseId">
                    <el-popover placement="right" width="400" trigger="click" v-model="showPopover">
                        <techo-house ref="vCheckedHouse" :isShowHouse="true" :butName="'确定'" :disCodeOption="disCodeOption" :buildOptions="buildOptions" :houseOptions="houseOptions" 
                            @onRelaData="handleCheckedHouse" @onQueryBuild="handleQueryBuildList" @onQueryHouse="handleQueryHouseList"></techo-house>
                        <el-button class="check-house" slot="reference" type="danger" size="small">请选择现住地户</el-button>
                    </el-popover>
                </el-form-item>
              </el-col>
              <el-col :span="18">
                <el-form-item label="现住地详址">
                    <el-input v-model="dialogForm.livePlaceDetail" disabled :maxlength="1024" placeholder="请填写现住地详址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/infact_people_service.js";
export default {
  props: {
    disCodeOption: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      buildOptions: [],
      houseOptions: [],
      showPopover: false,
      dic: {
        性别: [],
        国籍: [],
        宗教信仰: [],
        证件代码: [],
        职业类别: [],
        来华目的: [],
        行政区划: []
      },
      dialogForm: {
        isFocus: 0,
        sex: "1",
        livePlace: "610000",
        houseId: "",
      },
      teamRules: {
        foreignName: [
          { required: true, message: "请输入外文名", trigger: "blur" },
          { min: 2, max: 10, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        houseId: [
          { required: true, message: "请选择现住地户", trigger: "change" }
        ],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        phone: [
          { required: true, message: "请输入联系方式", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        comingToChina: [
          { required: true, message: "请选择来华目的", trigger: "change" }
        ],
        arriveTime: [
          { required: true, message: "请选择抵达日期", trigger: "change" }
        ],
        leaveTime: [
          { required: true, message: "请选择预计离开日期", trigger: "change" }
        ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            if (item.name == "职业类别") {
              that.dic[item.name] = that.handleTreeData(data);
            } else {
              that.dic[item.name] = data;
            }
          });
        }
      });
    });
  },
  methods: {
    // 职业类型组成树形数据
    handleTreeData(val) {
      // 大类
      let itemONE = _.map(
        _.filter(val, street => {
          return street.code.slice(1, 5) === "0000";
        }),
        street => {
          return {
            code: street.code,
            label: street.name,
            children: []
          };
        }
      );
      // 中类
      _.map(itemONE, item => {
        item.children = _.map(
          _.filter(val, street => {
            return (
              street.code.slice(0, 1) === item.code.slice(0, 1) &&
              street.code.slice(1, 5) !== "0000" &&
              street.code.slice(3, 5) === "00"
            );
          }),
          street => {
            return {
              code: street.code,
              label: street.name,
              children: []
            };
          }
        );
      });
      // 小类
      _.map(itemONE, midItem => {
        _.map(midItem.children, item => {
          item.children = _.map(
            _.filter(val, street => {
              return (
                street.code.slice(0, 3) === item.code.slice(0, 3) &&
                street.code.slice(3, 5) !== "00"
              );
            }),
            street => {
              return {
                code: street.code,
                label: street.name
              };
            }
          );
        });
      });
      return itemONE;
    },
    /** 选择现住地户 */
    handleCheckedHouse(value, buildInfo) {
      this.dialogForm.districtCode = buildInfo.districtCode;
      let houseInfo= _.find(this.houseOptions, item => {
        return item.id == value.houseId;
      });
      service.requestCodeByAddress(buildInfo.districtCode, data => {
        this.dialogForm.livePlaceDetail = "陕西省"+
          data.belongname + data.name + houseInfo.name;
          this.showPopover = false;
      });
      this.dialogForm.houseId = houseInfo.id;
      this.$refs["dialogForm"].clearValidate("houseId");
    },
    /** 查询楼栋列表 */
    handleQueryBuildList(code) {
      this.buildOptions = [];
      this.houseOptions = [];
      code != undefined && service.requestCodeByBuild(code, data => {
        this.buildOptions = data.list;
      });
    },
    /** 查询户列表 */
    handleQueryHouseList(param) {
      this.houseOptions = [];
      if(param.buildingId != "") {
        service.requestBuildByHouse(param, data => {
            this.houseOptions = data.list;
        });
      }
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.birthName = param.birthName && util.time.formatDate(param.birthName, "YYYY-MM-DD");
          param.certificateValidityTime = util.time.formatDate(
            param.certificateValidityTime,
            "YYYY-MM-DD"
          );
          param.arriveTime = param.arriveTime && util.time.formatDate(
            param.arriveTime,
            "YYYY-MM-DD"
          );
          param.leaveTime = param.leaveTime && util.time.formatDate(param.leaveTime, "YYYY-MM-DD");
          param.occupationType =
            param.occupationType && _.last(param.occupationType);
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveOverseasPeople(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "境外人员添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateOverseasPeople(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "境外人员修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$refs["vCheckedHouse"].resetForm();
      this.$data.dialogVisible = false;
      this.showPopover = false;
      this.isAdd = true;
      this.dialogForm = {
        isFocus: 0,
        sex: "1",
        livePlace: "610000",
        houseId: "",
      };
    }
  }
};
</script>
<style lang="scss">
.add-dialog {
  .el-date-editor.el-input {
    width: 100%;
  }
  .el-select {
    width: 100%;
  }
  .el-cascader {
    width: 100%;
  }
  .check-house {
    width: 100%;
  }
}
</style>